<template>
    <div class="modal"
         :class="[isShow?'':isShowModal?'hideBg':'']"
         :style="{height:height+'px'}"
         v-show="isShowModal">
        <div class="modal_content"
             :class="isShow?'showModal':'hideModal'">
          <div class="item" :style="{borderTop:'none'}">
            <span class="i_title">分类</span>
            <radio-group id="classification" @change="radioChange">
              <div class="radio-group">
                <label class="radio" v-for="item in items.classification" :key="item.id">
                  <radio :value="item.id" :checked="item.checked" color="#FF5D22" />
                  {{item.name}}
                </label>
              </div>
            </radio-group>
          </div>
          <div class="item">
            <span class="i_title">时间</span>
            <radio-group id="time" @change="radioChange">
              <div class="radio-group">
                <label class="radio" v-for="item in items.time" :key="item.id">
                  <radio :value="item.id" :checked="item.checked" color="#FF5D22" />
                  {{item.name}}
                </label>
              </div>
            </radio-group>
          </div>
          <!--<div class="item">-->
            <!--<span class="i_title">门店</span>-->
            <!--<radio-group id="store" @change="radioChange">-->
              <!--<div class="radio-group">-->
                <!--<label v-for="item in items.store" :key="item.id">-->
                  <!--<radio class="radio" :value="item.id" :checked="item.checked" color="#FF5D22" />-->
                  <!--{{item.name}}-->
                <!--</label>-->
              <!--</div>-->
            <!--</radio-group>-->
          <!--</div>-->
          <div class="foot">
            <div class="clear" @click="clear">清空</div>
            <div class="confirm" @click="confirmData">确认</div>
          </div>
          <img @click="hideMenu" class="m_img" src="../assets/images/close.png"/>
        </div>
  </div>
</template>

<script>
  import state from '@/store/order'
  export default {
    name: "modalMenu",
    data(){
      return{
        //删选选项
        items:{
            classification:[
              {id:0,name:'待支付',checked:true},
              {id:-2,name:'已退单',checked:false},
              {id:2,name:'已转单',checked:false},
              {id:1,name:'待转单',checked:false}
            ],
            time:[
              {id:30,name:'最近一个月',checked:true},
              {id:90,name:'最近三个月',checked:false},
              {id:180,name:'最近半年内',checked:false},
              {id:365,name:'最近一年内',checked:false}
            ],
            store:[
              {id:0,name:'门店1',checked:true},
              {id:1,name:'门店2',checked:false},
              {id:2,name:'门店3',checked:false},
              {id:3,name:'门店4',checked:false}
            ]
        },
        //删选选择
        checkItems:{
          //分类
          classification:0,
          //时间
          time:30,
          //门店
          store:0
        },
        //屏幕高度
        height:wx.getSystemInfoSync().windowHeight,
      }
    },
    methods:{
      //分类选择
      radioChange(e){
        console.log('e',e);
        let id = e.target.id;
        let item = this.$data.checkItems;
        let index = e.target.value;
        switch (id) {
          case 'classification':
              item.classification = index;
              break;
          case 'time':
              item.time = index;
              break;
          case 'store':
              item.store = index;
              break;
        }
        this.$data.checkItems = item;
      },
      //隐藏菜单
      hideMenu(){
        state.commit('hideState');
      },
      //清空
      clear(){
        this.$data.checkItems = {};
        this.hideMenu();
      },
      //确定
      confirmData(){
        console.log('点击了');
        let item = this.$data.checkItems;
        this.$emit('filter',item);
        state.commit('hideState');
      }
    },
    computed:{
      isShowModal(){
        return state.state.isShowModal;
      },
      isShow(){
          return state.state.isShow;
      }
    }
  };
</script>

<style lang="scss">
  $fontColor: #999;
  $fontSize: 13px;
  .modal{
    position: absolute;
    background-color: rgba(0,0,0,0.6);
    width: 100%;
    top: 0;
    left: 0;
    z-index: 99;
    font-size: $fontSize;
    font-color:$fontColor;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    .modal_content{
      position: relative;
      transition: all .3s ease 0s;
      padding-top: 20px;
      background-color: #ffffff;
      .item{
        padding-top: 10px;
        background-color: #ffffff;
        z-index:99;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        padding-bottom: 20px;
        border-top:1px solid rgba(180,180,180,0.1);
        .i_title{
          padding-left: 20px;
          font-size: 14px;
          font-weight: 600;
        }
        .radio-group{
          display: flex;
          justify-content: space-between;
          width: 95%;
          flex-wrap: wrap;
          margin: 0 auto;
          .radio{
            margin-left: 10px;
            margin-top: 10px;
          }
        }
      }
      .foot{
        background-color: #ffffff;
        display: flex;
        font-size: 15px;
        .clear{
          flex: 1;
          text-align: center;
          background-color: #E9E9E9;
          color: #999999;
          padding-top: 10px;
          padding-bottom: 10px;
        }
        .confirm{
          flex: 1;
          border-left: 1px solid rgba(180,180,180,0.3);
          text-align: center;
          background-color: #FF5D22;
          color: #ffffff;
          padding-top: 10px;
          padding-bottom: 10px;
        }
      }
      .m_img{
        position: absolute;
        width: 20px;
        height: 20px;
        top: 10px;
        right: 15px;
      }
    }
  }
  .showModal{
    transform: translateY(0px);
  }
  .hideModal{
    transform: translateY(350px);
  }
  .hideBg{
    animation: hide .3s;
  }
  .pauseAnimation{
    animation: hide 0s!important;
  }
  @keyframes show{
    from{background-color: rgba(0,0,0,0)}
    to{background-color:rgba(0,0,0,0.6)}
  }
  @keyframes hide{
    from{background-color: rgba(0,0,0,0.6)}
    to{background-color: rgba(0,0,0,0)}
  }
  radio{
    width:25px;
    height:25px;
  }
</style>



