import {util} from "./util";

class api extends util{

  constructor(){
    super();
  }

  //获取验证码
  getCode(phone){
    let params = {
      url:'json/getCode.json',
      data:{
        mobile:phone,
        codeType:1
      }
    }
    return this.request(params);
  }

  //获取订金单列表
  getSubscription(user_id,mobile,days,status,page,pagesize){
    let params = {
      url:'soinfo/list.json',
      data:{
        user_id:user_id,
        mobile:mobile,
        days:days,
        status:status,
        page:page,
        pagesize:pagesize
      }
    }
    return this.request(params);
  }
  //获取转单数据
  getOrderData(id,callback){
    let params = {
      url:'soinfo/view.json',
      data:{
       id:id
      },
      callback:function(res){
        callback && callback(res)
      }
    }
    this.request(params)
  }
  //获取订金单详情
  getSubDesc(user_id){
    let params = {
      url:'soinfo/add.json',
      data:{
        user_id:user_id
      }
    }
    return this.request(params);
  }
  //提交定金单
  submitData(user_id,user_receive_id,user_link_id,project_id,village_id){
    let params = {
      url:'soinfo/doadd.json',
      data:{
        user_id:user_id,
        user_receive_id:user_receive_id,
        user_link_id:user_link_id,
        project_id:project_id,
        village_id:village_id
      }
    }
    return this.request(params);
  }
  //获取订单列表
  getOrderList(status,page,pagesize){
    let params = {
      url:'soitem/list.json',
      data:{
        status:status,
        page:page,
        pagesize:pagesize
      }
    }
    return this.request(params);
  }
  //获取订单数据
  getOrderData(id){
    let params = {
      url:'soitem/list.json',
      data:{
        status:status,
        page:page,
        pagesize:pagesize
      }
    }
    return this.request(params);
  }

  //交付订单
  pay(id){
    let params = {
      url:'soitem/doupdate.json',
      data:{
        id:id
      },
      useToken:true
    }
    return this.request(params);
  }

  //获取订金单详情数据
  getSubDetail(id){
    let params = {
      url:'soinfo/view.json',
      data:{
        id:id
      },
      useToken:true
    }
    return this.request(params);
  }

  //登陆
  login(mobile,code){
      let params = {
        url:'businessuser/loginByCode.json',
        data:{
          anycode:'soask_duorang',
          mobile:mobile,
          code:code
        }
      }
      return this.request(params);
  }
  //添加订单
  getSoInfo(id){
    let params = {
      url:'soitem/add.json',
      data:{
        id:id
      },
      useToken:true
    }
    return this.request(params);
  }

  //提交订单
  submitOrderS(data){
    let params = {
      url:'soitem/doadd.json',
      data:data,
      useToken:true,
      useLoading:true
    }
    return this.request(params);
  }

  //首页获取用户信息
  getBusinessShop(){
    let id = wx.getStorageSync('tokenId');
    let params = {
      url:'businessuser/view.json',
      data:{
        id:id
      },
      useToken:true
    }
    return this.request(params);
  }
  //获取门店列表
  getBussiness(){
    let params = {
      url:'businessshop/list.json',
      data:{
        page:1,
        pagesize:10
      },
      useToken:true
    }
    return this.request(params);
  }

  //交付订单
  submitOrder(id){
    let params = {
      url:'soitem/doupdate.json',
      data:{
        id:id
      },
      useToken:true,
      useLoading:true
    }
    return this.request(params);
  }

  //获取订金单列表
  getSubscriptionList(data){
    let params = {
      url:'soinfo/list.json',
      data:data,
      useToken:true
    }
    return this.request(params);
  }

  //获取订单数据列表
  getOrderList(status,page,pagesize){
    let params = {
      url:'soitem/list.json',
      data:{
        status:status,
        page:page,
        pagesize:pagesize
      },
      useToken:true
    }
    return this.request(params);
  }

  //扫码
  scanCode(code){
    let params = {
      url:'soinfo/listByusercode.json',
      data:{
        usercode:code
      },
      useToken:true,
      useLoading:true
    }
    return this.request(params);
  }

  //注销
  logout(){
    wx.setStorageSync('token', null);
    wx.setStorageSync('tokenId', null);
    wx.redirectTo({
      url:'../login/main'
    })
  }
}
export {api}



