<template>
    <div :class="bg?'wbg':'bbg'" @click="toDesc" v-if="data">
      <img class="b_img" :src="data.img_url"/>
      <div class="b_content">
        <div class="b_text">
          <span class="b_c_name">{{data.business_name?data.business_name:data.project_name}}</span>
          <span class="b_c_content" v-if="data.shop_name?true:false">{{data.shop_name}}</span>
          <span class="b_c_content" v-else-if="data.project_content">{{data.project_content}}</span>
        </div>
        <span class="b_c_price" v-if="data.so_price===undefined">{{data.pay_price?'￥'+data.pay_price:data.position_name+data.shop_address}}</span>
        <span class="b_c_price" v-else>￥{{data.so_price}}</span>
      </div>
    </div>
</template>

<script>
  export default {
    props:['bg','data'],
    methods:{
      toDesc:function(){
        this.$emit('toDesc',this.$props.data.id);
      }
    },
    watch:{
      data(val){
        this.data = val;
      }
    }
  };
</script>

<style lang="scss">
    @mixin base($bg){
      background-color: $bg;
      display: flex;
      padding-top: 10px;
      padding-bottom: 10px;
      border-top: 1px solid rgba(180,180,180,0.2);
      padding-left: 20px;
      @if $bg == #ffffff{
        margin-bottom: 10px;
      }
      & .b_img{
        @if $bg == #ffffff{
          width: 100px;
          height: 100px;
        }@else{
          width: 70px;
          height: 70px;
        }
        border: 1px solid rgba(180,180,180,0.4);
      }
      & .b_content{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding-left: 6px;
        .b_text{
          display: flex;
          flex-direction: column;
        }
        .b_c_name{
          color: #333333;
          font-size:14px;
          font-weight: 600;
        }
        .b_c_content{
          color: #666666;
          padding-top: 6px;
          font-size: 13px;
          padding-right: 10px;
        }
        .b_c_price{
          font-size: 14px;
          color: #000;
        }
      }
    }
    .wbg{
      @include base(#ffffff)
    }
    .bbg{
      @include base(#f7f7f7)
    }
</style>



