<template>
    <div class="item" v-if="data">
      <div class="i_head">
        <div class="i_left">
          <p class="l_phone">{{data.name}} {{data.mobile}}</p>
          <div class="left">
            <img src="../assets/images/icon_dz@3x.png"/>
            <span>{{data.village_name}} {{data.door_number}}</span>
          </div>
        </div>
        <div class="i_right">{{data.status}}</div>
      </div>
      <div class="i_content">
        <img-item :bg="false"
                  :data="data"
                  @toDesc="toDesc">
        </img-item>
        <div class="i_c_total">
          <span class="i_c_t_txt">订单总额:</span>
          <span class="i_c_t_price"> ￥{{data.total_price}}元</span>
        </div>
      </div>
    </div>
</template>

<script>
  import imgItem from './imgItem';
  export default {
    props:['data','order'],
    components:{
      imgItem
    },
    methods:{
      toDesc(id){
        wx.navigateTo({url:'../orderDetail/main?id='+id})
      },
    },
    watch:{
      data(val){
        this.data = val;
      }
    }
  };
</script>

<style lang="scss">
  $fontColor: #ff5d22;
    .item{
      background-color: #ffffff;
      padding-top: 16px;
      margin-bottom: 10px;
      .i_head{
        display: flex;
        .i_left{
          display: flex;
          flex-direction: column;
          font-size: 13px;
          color: #666666;
          padding-left: 20px;
          .left{
            padding-top: 10px;
            flex: 1;
            display: flex;
            align-items: center;
            img{
              width:28rpx;
              height:34rpx;
              padding-top:2px;
              padding-right:4px;
            }
          }
        }
        .i_right{
          color: $fontColor;
          font-size: 16px;
          flex:1;
          text-align:right;
          padding-right: 20px;
          font-size: 15px;
        }
      }
      .i_content{
        padding-top: 20px;
        .i_c_total{
          text-align: right;
          padding-right: 20px;
          font-size: 14px;
          padding-top: 10px;
          padding-bottom: 20px;
          .i_c_t_txt{
            color: #999999;
          }
          .i_c_t_price{
            color: $fontColor;
          }
        }
        .i_c_btn{
          text-align: right;
          padding-right: 20px;
          font-size: 13px;
          padding-top: 18px;
          padding-bottom: 10px;
          .btn{
            display: inline-block;
            color: $fontColor;
            padding: 4px 8px;
            border: 1px solid $fontColor;
            -webkit-border-radius: 15px;
            -moz-border-radius: 15px;
            border-radius: 15px;
          }
        }
      }
    }
</style>



